import { Container, Row, Col } from "react-bootstrap";
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import meter2 from '../assets/img/meter2.svg'
import rIcon from '../assets/img/r.svg';
import pIcon from '../assets/img/python.svg';
import iaIcon from '../assets/img/ia.svg';
import dataIcon from '../assets/img/datavis.svg';
import mathsIcon from '../assets/img/maths.svg';

export const Skills = () => {
    const responsive = {
        superLargeDesktop: {
          // the naming can be any, depends on you.
          breakpoint: { max: 4000, min: 3000 },
          items: 5
        },
        desktop: {
          breakpoint: { max: 3000, min: 1024 },
          items: 3
        },
        tablet: {
          breakpoint: { max: 1024, min: 464 },
          items: 2
        },
        mobile: {
          breakpoint: { max: 464, min: 0 },
          items: 1
        }
      };
      return(
        <section className="skill" id="skills">
            <Container>
                <Row>
                    <Col>
                    <div className="skill-bx">
                        <h2>
                            Skills
                        </h2>
                        <Carousel responsive={responsive} infinite={true} className="skill-slider">
                            <div className="item">
                                <img src={pIcon} alt="Python" className="IconSkills"/>
                                <h5>Python</h5>
                            </div>
                            <div className="item">
                                <img src={rIcon} alt="Python" className="IconSkills"/>
                                <h5>R</h5>
                            </div>
                            <div className="item">
                                <img src={iaIcon} alt="IA"/>
                                <h5>IA</h5>
                               
                            </div>
                            <div className="item">
                                <img src={dataIcon} alt="DATA"/>
                                <h5>Visualisation</h5>
                            </div>
                            <div className="item">
                                <img src={mathsIcon} alt="Stats"/>
                                <h5>Maths</h5>
                            </div>
                        </Carousel>
                    </div>
                    </Col>
                </Row>
            </Container>
        </section>
      )
}