import React from 'react';
import Project from './Project';
import killerImage from '../assets/img/pingouin-tueur.png';
import IaDotImg from '../assets/img/DotLanguageUndirected.svg';

const projects = [
  {
    title: "Jeu Killer",
    description: "Projet de programmation systme 2023-2024, L2 info et math info -- Universit Cte d'Azur",
    imageUrl: killerImage,
    githubUrl: "https://webusers.i3s.unice.fr/~elozes/enseignement/systeme/#L2-info-et-math-info----Universit%C3%A9-C%C3%B4te-d'Azur"
  },
  {
    title: "IA Graphviz DOT",
    description: "Projet rsolution de problmes  base de graphes d'tats L2 IA -- Universit Cte d'Azur",
    imageUrl: IaDotImg,  
    githubUrl: "https://github.com/utilisateur/projet2"
  },
  // Ajoute plus de projets ici
];

const ProjectList = () => {
  return (
    <div className="project-list">
      {projects.map((project, index) => (
        <Project
          key={index}
          title={project.title}
          description={project.description}
          imageUrl={project.imageUrl}
          githubUrl={project.githubUrl}
        />
      ))}
    </div>
  );
};

export default ProjectList;
